.home-container{
    height:300px;
    color:white;
    
}
.home-content h2{
    padding:5px;
    padding-top:60px;
    margin-left:100px;
}
.home-content h1{
    padding:5px;
    color:rgb(160, 160, 190);
    margin-left:100px;
    
}
.home-buttons{
    margin-left:100px;
    margin-top:20px;
}
.home-buttons .btn-hire{
    background-color:rgb(103, 141, 140);
    color:white;
    width:120px;
}
.home-buttons .btn-hire:hover{
    background-color:white;
    color:rgb(103, 141, 140);
    width:160px;
    transition:1s;
}
.home-buttons .btn-cv{
    background-color:rgb(103, 141, 140);
    color:white;
    margin-left:15px;  
}
.home-buttons .btn-cv:hover{
    opacity:50%;
      
}
@media (max-width: 600px) {
    .home-container {
      padding: 0;
      margin: 0;
      margin-top: 50px;
    }
    .home-container h2 {
      font-size: 24px;
    }
    .home-container h1 {
      font-size: 24px;
    }
  }