.sidebar-section{
    display: flex;
    background-color:rgb(43, 42, 78);
}

.sidebar {
    height: 100vh;
    background-color:#1e1e2c;
    width: 90px;
    position:fixed;
  }
  .sidebar-toggle{
    width: 200px;

}
  
  .sidebar-toggle-icons {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    cursor: pointer;
  }
  
  .sidebar-toggle-icons p svg {
    background-color:rgb(92, 90, 160);
    color: white;
    margin-top: 30px;
  }
  
  @media (max-width: 600px) {
    .sidebar {
      display: none;
    }
  }